import React from 'react'
import styled from 'styled-components'

const CreadorDiv = styled.div`
    text-align: center;
    color: #535353;
    transition: all 1s ease-in-out;
`
const Creador = () => {
    return (
        <CreadorDiv id="creador">BY Jackson</CreadorDiv>
    )
}

export default Creador